/*#314257*/
body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
fieldset,
img,
abbr {
  border: 0;
}
address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}
ul li {
  list-style: none;
}
caption,
th {
  text-align: left;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
sup {
  vertical-align: text-top;
}
sub {
  vertical-align: text-bottom;
}
input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}
legend {
  color: #000;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}
img {
  max-width: 100%;
  height: auto;
}
.wrapper {
  max-width: 1100px;
  margin: 0 auto;
  padding-left: 10px;
  padding-right: 10px;
}
@media screen and (max-width: 1100px) {
  .wrapper {
    padding-left: 6px;
    padding-right: 6px;
  }
}
@media screen and (max-width: 780px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}
@media screen and (max-width: 450px) {
  .wrapper {
    padding-left: 4px;
    padding-right: 4px;
  }
}
/*.debug { .debug( rgba(0, 0, 0, .2), rgba(255, 255, 0, 0.6)) }*/
html {
  font-size: 14px;
}
html,
body {
  width: 100%;
  height: auto;
}
body {
  background: url('../img/bg.jpg') center top no-repeat;
  background-size: auto 100%;
  background-attachment: fixed;
}
.header {
  background-color: transparent;
  width: 100%;
  height: auto;
}
.header__wrapper {
  min-height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding-left: 10px;
  padding-right: 10px;
}
@media screen and (max-width: 1100px) {
  .header__wrapper {
    padding-left: 6px;
    padding-right: 6px;
  }
}
@media screen and (max-width: 780px) {
  .header__wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}
@media screen and (max-width: 450px) {
  .header__wrapper {
    padding-left: 4px;
    padding-right: 4px;
  }
}
.header__logo {
  min-width: 100px;
  height: 100px;
  display: flex;
  align-items: center;
  flex-grow: 1;
}
.header__logo > img {
  height: calc(80px);
}
@media screen and (max-width: 450px) {
  .header__logo {
    justify-content: center;
  }
}
.header__search {
  flex-grow: 2;
  min-width: 200px;
  flex-basis: 300px;
  height: calc(80px);
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__search > input {
  width: 80%;
  height: calc(33.33333333px);
}
@media screen and (max-width: 780px) {
  .header__search {
    justify-content: flex-end;
  }
}
@media screen and (max-width: 450px) {
  .header__search {
    justify-content: center;
  }
}
.header__buttons {
  min-width: 320px;
  height: calc(80px);
  display: flex;
  flex-grow: 3;
  justify-content: space-between;
}
.header__btn {
  background: transparent;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 2.5px;
  margin-right: 2.5px;
  width: calc(20% - 5px);
}
@media screen and (max-width: 1100px) {
  .header__btn {
    margin-left: 2.5px;
    margin-right: 2.5px;
  }
}
@media screen and (max-width: 780px) {
  .header__btn {
    margin-left: 2px;
    margin-right: 2px;
  }
}
@media screen and (max-width: 450px) {
  .header__btn {
    margin-left: 1px;
    margin-right: 1px;
  }
}
@media screen and (max-width: 350px) {
  .header__btn {
    width: calc(18.333333333333336% - 2px);
  }
}
.header__personal {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
/*  CONTAINER  */
.container {
  width: 100%;
  height: auto;
  margin: 50px 0;
  background: rgba(252, 252, 252, 0.95);
}
.navigation {
  height: 80px;
  background: #fff;
}
.holder {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  /*flex-direction: column;
  flex: 1 0 auto;
  overflow-x: hidden;
  overflow-y: auto;
  position: relative;
  z-index: 0;*/
}
.cell {
  background-color: rgba(190, 199, 250, 0.4);
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 2.5px;
  margin-right: 2.5px;
  width: calc(20% - 5px);
  margin: 2.5px;
}
@media screen and (max-width: 1100px) {
  .cell {
    margin-left: 2.5px;
    margin-right: 2.5px;
  }
}
@media screen and (max-width: 780px) {
  .cell {
    margin-left: 2px;
    margin-right: 2px;
  }
}
@media screen and (max-width: 450px) {
  .cell {
    margin-left: 1px;
    margin-right: 1px;
  }
}
@media screen and (max-width: 1100px) {
  .cell {
    width: calc(25% - 5px);
  }
}
@media screen and (max-width: 960px) {
  .cell {
    width: calc(25% - 5px);
  }
}
@media screen and (max-width: 780px) {
  .cell {
    width: calc(33.333333333333336% - 4px);
  }
}
@media screen and (max-width: 560px) {
  .cell {
    width: calc(50% - 4px);
  }
}
@media screen and (max-width: 450px) {
  .cell {
    width: calc(50% - 2px);
  }
}
@media screen and (max-width: 350px) {
  .cell {
    width: calc(100% - 2px);
  }
}
@media screen and (max-width: 1100px) {
  .cell {
    margin: 2.5px;
  }
}
@media screen and (max-width: 780px) {
  .cell {
    margin: 2px;
  }
}
@media screen and (max-width: 450px) {
  .cell {
    margin: 1px;
  }
}
.relation {
  position: relative;
}
.relation__ratio {
  height: 0;
  /* Разделить на первое, умножить на второе*/
  /* Если первое больше второго, то будет горизонтальный прямоугольник */
}
.relation__ratio-1x1 {
  padding-top: 100%;
}
.relation__ratio-2x1 {
  padding-top: 50%;
}
.relation__ratio-4x3 {
  padding-top: 75%;
}
.relation__ratio-3x4 {
  padding-top: 133.33%;
}
.relation__ratio-6x5 {
  padding-top: 83.33%;
}
.relation__ratio-8x7 {
  padding-top: 87.5%;
}
.relation__ratio-7x8 {
  padding-top: 114%;
}
.relation__ratio-8x9 {
  padding-top: 112.5%;
}
.relation__ratio-9x10 {
  padding-top: 111.11%;
}
.relation__ratio-10x11 {
  padding-top: 110%;
}
.relation__ratio-11x12 {
  padding-top: 109.09%;
}
.relation__ratio-16x9 {
  padding-top: 56.25%;
}
.relation__content {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.relation__content > img {
  width: 90%;
  max-height: 80%;
}
